import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { Product, ProductsService } from '@modules/products/shared';
import { Library } from '@core/index';
import { DxFormComponent } from 'devextreme-angular';
import { BsModalService } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-add-wavehourse',
  templateUrl: './add-wavehourse.component.html',
  styleUrls: ['./add-wavehourse.component.scss']
})
export class AddWavehourseComponent implements OnInit, AfterViewInit {
  @ViewChild(DxFormComponent) form: DxFormComponent;
  listTypeId: string;
  wavehourse: any;
  onSave: any;

  constructor(
    public bsModalRef: BsModalRef,
    private productsService: ProductsService,
    private bsModalService: BsModalService,
  ) { }

  ngOnInit() {
  }

  focusField =() =>{
    this.bsModalService.onShown.subscribe(() => {
      this.form.instance.getEditor("code").focus();
    });
  }

  ngAfterViewInit() {
    this.focusField();
  }

  onSubmit = function(e) {
    e.preventDefault();
    let data = {wavehourse: this.wavehourse};
    this.productsService.addWavehourse(data).subscribe((response: any) => {
      if (response.status == "success") {
        Library.notify("Thêm dữ liệu thành công", "success");
        this.wavehourse.id = response.data.id;
        this.onSave(this.wavehourse);
        this.bsModalRef.hide();
      } else {
        Library.notify(response.message, "error");
      }
    }, err => { }, () => {
    });
  }
}
