import {Component, OnInit, ViewChild, ChangeDetectorRef, AfterViewInit} from '@angular/core';

import {BsModalService} from 'ngx-bootstrap/modal';
import {BsModalRef} from 'ngx-bootstrap/modal/bs-modal-ref.service';
import {Product, ProductsService, Wavehourse} from '@modules/products/shared';
import {List, ListsService} from '@modules/lists/shared';
import {DxFormComponent, DxPopupComponent} from 'devextreme-angular';
import {Library, AuthService, VChanggeMoneyService} from '@core/index';
import {pattern} from '@config/index';
import {AddGroupProductComponent} from '../add-group-product/add-group-product.component';
import {AddUnitCodeComponent} from '../add-unit-code/add-unit-code.component';
import {AddWavehourseComponent} from '../add-wavehourse/add-wavehourse.component';
import {TranslateService} from '@ngx-translate/core';
import {AddRevenueComponent} from '../add-revenue/add-revenue.component';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit, AfterViewInit {
  @ViewChild(DxFormComponent) form: DxFormComponent;
  product: any;
  id: any;
  order: number;
  totalCount: any;
  showOrder: boolean = false;
  groupProducts: any;
  revenues:any;
  groupProduct: any;
  revenue:any;
  selectedGroupProduct: string = '';
  selectedrevenue: string = '';
  unitCalculations: any;
  unitCalculation: any;
  selectedUnitCalculation: string = '';
  vats: any;
  groupProductListTypeId: any;
  revenuListTypeId:any;
  unitCalculationListTypeId: any;
  vatListTypeId: any;
  configDecimal: any;
  wavehourses: any;
  wavehourse: any;
  selectedWavehourse: string = '';
  _translate: any;
  formatter: any = {};
  txt: any = {};
  code: any;
  name: any;
  productCodePattern: any = pattern.productCodePattern;
  isTruongHoc = false;

  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    public bsModalWarehouse: BsModalRef,
    public bsModalGroupProduct: BsModalRef,
    public bsModalUnitCalculation: BsModalRef,
    private productsService: ProductsService,
    private listsService: ListsService,
    private modalService: BsModalService,
    private ref: ChangeDetectorRef,
    public translate: TranslateService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }

  ngOnInit() {
    let account = this.authService.getCurrentUser().account
    if (account) {
      if (account.account_type == "HS-SV") {
        this.code = this._translate.GRID.REVENUE.revenue_code;
        this.name = this._translate.GRID.REVENUE.revenue_name;
        this.isTruongHoc = true;
      }
      else {
        this.code = this._translate.GRID.INVOICE_DETAILS.product_code;
        this.name = this._translate.GRID.INVOICE_DETAILS.item_name;
        this.isTruongHoc = false;
      }
    }
    this.txt = {
      product_group: this._translate.CONTROLL.LABEL.select + ' ' + this._translate.GRID.PRODUCT.product_group.toLowerCase(),
      warehouse: this._translate.CONTROLL.LABEL.select + ' ' + this._translate.CONTROLL.LABEL.warehouse.toLowerCase(),
      noDataText: this._translate.CONTROLL.LABEL.noDataText,
      product_code: this._translate.CONTROLL.LABEL.placeHolder_productCode,
      form_revenue: this._translate.CONTROLL.LABEL.select + ' ' + this._translate.GRID.REVENUE.collection_form.toLowerCase(),
      item_name: this._translate.CONTROLL.LABEL.input + ' ' + this.name.toLowerCase(),
      active: this._translate.GRID.GENERAL.active,
      unit_code: this._translate.CONTROLL.LABEL.select + ' ' + this._translate.GRID.INVOICE_DETAILS.unit_code.toLowerCase(),
      select: this._translate.CONTROLL.LABEL.select,
    };
    this.getProduct();
    this.formatter.don_gia = VChanggeMoneyService.formatDecimalColumn(this.configDecimal.don_gia);
   
  }

  focusField = () => {
    this.modalService.onShown.subscribe(() => {
      this.form.instance.getEditor('code').focus();
    });
  };

  ngAfterViewInit() {
    this.focusField();
  }

  addGroupProduct = {
    text: '',
    icon: 'assets/img/icons/Them.png',
    type: 'default',
    onClick: () => {
      this.groupProduct = new List();
      this.groupProduct.order = this.groupProducts.length + 1;
      const initialState = {
        groupProduct: this.groupProduct,
        listTypeId: this.groupProductListTypeId,
        onSave: (groupProduct) => {
          this.groupProduct = groupProduct;
          this.groupProducts.push({
            id: groupProduct.id,
            name: groupProduct.name,
            code: groupProduct.code,
          });
          this.selectedGroupProduct = this.groupProduct.code;
        }
      };
      this.bsModalGroupProduct = this.modalService.show(AddGroupProductComponent, {
        class: 'second child-modal', initialState, backdrop: 'static',
        keyboard: false
      });
    }
  };

  addRevenue = {
    text: '',
    icon: 'assets/img/icons/Them.png',
    type: 'default',
    onClick: () => {
      this.revenue = new List();
      this.revenue.order = this.revenues.length + 1;
      const initialState = {
        revenue: this.revenue,
        listTypeId: this.revenuListTypeId,
        onSave: (revenue) => {
          this.revenue = revenue;
          this.revenues.push({
            id: revenue.id,
            name: revenue.name,
            code: revenue.code,
          });
          this.selectedrevenue = this.revenue.code;
        }
      };
      this.bsModalGroupProduct = this.modalService.show(AddRevenueComponent, {
        class: 'second child-modal', initialState, backdrop: 'static',
        keyboard: false
      });
    }
  };

  addUnitCalculation = {
    text: '',
    icon: 'assets/img/icons/Them.png',
    type: 'default',
    onClick: () => {
      this.unitCalculation = new List();
      this.unitCalculation.order = this.unitCalculations.length + 1;
      const initialState = {
        unitCalculation: this.unitCalculation,
        listTypeId: this.unitCalculationListTypeId,
        onSave: (unitCalculation) => {
          this.unitCalculation = unitCalculation;
          this.unitCalculations.push({
            id: unitCalculation.id,
            name: unitCalculation.name,
            code: unitCalculation.code,
          });
          this.selectedUnitCalculation = this.unitCalculation.code;
        }
      };
      this.bsModalUnitCalculation = this.modalService.show(AddUnitCodeComponent, {
        class: 'second child-modal', initialState, backdrop: 'static',
        keyboard: false
      });
    }
  };

  // addWavehourse = {
  //   text: "",
  //   icon: "assets/img/icons/Them.png",
  //   type: "default",
  //   onClick: () => {
  //     this.wavehourse = new Wavehourse();
  //     this.wavehourse.order = this.wavehourses.length + 1;
  //     const initialState = {
  //       wavehourse: this.wavehourse,
  //       onSave: (wavehourse) => {
  //         this.wavehourse = wavehourse;
  //         this.wavehourses.push({
  //           id: wavehourse.id,
  //           name: wavehourse.name,
  //           code: wavehourse.code,
  //         });
  //         this.selectedWavehourse = this.wavehourse.id;
  //       }
  //     };
  //     this.bsModalWarehouse = this.modalService.show(AddWavehourseComponent, {
  //       class: 'second child-modal', initialState, backdrop: 'static',
  //       keyboard: false
  //     });
  //   }
  // };

  getProduct() {
    if (this.product.id != '') { // nếu là edit
      if (this.product.status && this.product.status === 'HOAT_DONG') {
        this.product.status = true;
      } else if (this.product.status && this.product.status === 'KHONG_HOAT_DONG') {
        this.product.status = false;
      }
      this.selectedGroupProduct = this.product.group_code;
      this.selectedrevenue = this.product.form_revenue;
      this.selectedUnitCalculation = this.product.unit_code;
      this.selectedWavehourse = this.product.wavehourse_id;
      this.product.vat = this.product.vat ? this.product.vat.toString() : '';
    } else {
      this.product.order = this.totalCount + 1;
      this.product.status = true;
      this.product.discount = 0.00;
    }
  }

  //xử lý submit add product
  onSubmit(e) {
    Library.showloading();
    if (this.product.status) {
      this.product.status = 'HOAT_DONG';
    } else {
      this.product.status = 'KHONG_HOAT_DONG';
    }

    let data = {product: this.product};
    if (!this.product.id) {
      this.productsService.add(data).subscribe((response: any) => {
        if (response.status == 'success') {
          this.bsModalRef.content.successSaveItem = 'Success';
          Library.notify(this._translate.NOTIFY.add_success, 'success');
          this.bsModalRef.hide();

        } else {
          Library.notify(response.message, 'error');
        }
      }, err => {
      }, () => {
        Library.hideloading();
      });
    } else {
      let id = this.product.id;
      this.productsService.edit(data, id).subscribe((response: any) => {
        if (response.status == 'success') {
          this.bsModalRef.content.successSaveItem = 'Success';
          Library.notify(this._translate.NOTIFY.update_success, 'success');
          this.bsModalRef.hide();
        } else {
          Library.notify(response.message, 'error');
        }
      }, err => {
      }, () => {
        Library.hideloading();
      });
    }
  }

  unitCodeChange = (e) => {
    let _obj = this.unitCalculations.filter(p => p.code == e.value)[0];
    if (_obj) {
      //this.product.unit_name = _obj.name;
    }
  };
}
