import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IndexComponent } from './components/index/index.component';
import { DxTreeListModule } from 'devextreme-angular';
//library
import { devextremeModule } from '@core/index';

//general
import { routing } from './products.routing';
import { SharedModule } from '@shared/index';
import { ProductsService, ProductsResolver } from './shared';
import { AddComponent } from './components/add/add.component';
import { AddGroupProductComponent } from './components/add-group-product/add-group-product.component';
import { AddUnitCodeComponent } from './components/add-unit-code/add-unit-code.component';
import { AddWavehourseComponent } from './components/add-wavehourse/add-wavehourse.component';
import { vTranslateModule } from '@core/module/v-translate.module';
import { AddRevenueComponent } from './components/add-revenue/add-revenue.component';

@NgModule({
  imports: [
    CommonModule,
    routing,
    devextremeModule,
    DxTreeListModule,
    SharedModule,
    vTranslateModule
  ],
  declarations: [IndexComponent, AddComponent, AddGroupProductComponent, AddUnitCodeComponent, AddWavehourseComponent, AddRevenueComponent],
  entryComponents: [
    AddComponent,
    AddGroupProductComponent,
    AddUnitCodeComponent,
    AddWavehourseComponent,
    AddRevenueComponent
  ],
  providers: [ProductsService, ProductsResolver]
})
export class ProductsModule { }
