import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { List, ListsService } from '@modules/lists/shared';
import { Library, VStringService } from '@core/index';
import { BsModalService } from 'ngx-bootstrap/modal';
import { DxFormComponent } from 'devextreme-angular';

@Component({
  selector: 'app-add-unit-code',
  templateUrl: './add-unit-code.component.html',
  styleUrls: ['./add-unit-code.component.scss']
})
export class AddUnitCodeComponent implements OnInit, AfterViewInit {
  @ViewChild(DxFormComponent) form: DxFormComponent;
  listTypeId: string;
  unitCalculation: any;
  onSave: any;
  constructor(
    public bsModalRef: BsModalRef,
    private listsService: ListsService,
    private bsModalService: BsModalService
  ) { }

  ngOnInit() {
  }

  focusField = () => {
    this.bsModalService.onShown.subscribe(() => {
      this.form.instance.getEditor("code").focus();
    });
  }

  ngAfterViewInit() {
    this.focusField();
  }

  onSubmit = function (e) {
    e.preventDefault();
    if (this.unitCalculation.status) {
      this.unitCalculation.status = "HOAT_DONG";
    } else {
      this.unitCalculation.status = "KHONG_HOAT_DONG";
    }
    this.unitCalculation.listtype_id = this.listTypeId;
    let data = { list: this.unitCalculation };
    this.listsService.add(data).subscribe((response: any) => {
      if (response.status == "success") {
        Library.notify("Thêm dữ liệu thành công", "success");
        this.onSave(this.unitCalculation);
        this.bsModalRef.hide();
      } else {
        Library.notify(response.message, "error");
      }
    }, err => { }, () => {
    });
  }
  changeUnitCode = (e) => {
    this.unitCalculation.code = VStringService._convertToViKdau(e.value);
  }
}
