import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Product, ProductsService } from '@modules/products/shared';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { AddComponent } from '../add/add.component';
import { Library, AuthService, Helper } from '@core/index';
import { DxDataGridComponent } from 'devextreme-angular';
import { VSelectBoxService } from '@core/library/public/v-select-box.service';

import * as XLSX from 'xlsx';
import { pattern } from '@config/index';
import * as moment from 'moment';
import { TranslateService } from '@ngx-translate/core';
import { sEnum } from '@config/sEnum';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  dataSource: any = {};
  selectedRows = [];
  noDataText: string = "Chưa có hàng hóa nào";
  bsModalRef: BsModalRef;
  statusFilter: any;
  groupProducts: any;
  revenues: any;
  unitCalculations: any;
  groupProductListTypeId: string;
  revenuListTypeId: string;
  unitCalculationListTypeId: string;
  vatListTypeId: string;
  vats: any
  wavehourses: any;
  configDecimal: any;
  formatter: any = {};
  configCRM: any;
  isIntegrateCRM: any;
  showPopupGetFile = false;

  dataExcelFile: any;
  showPopup = false;
  fileName: any;
  fileSize: any;
  valueProgress: number = 0;
  maximum_object_import: any;
  currentUser: any;
  productCodePattern: any = pattern.productCodePattern;
  header = [{
    wavehourse_code: "DANH SÁCH DANH MỤC"
  }, {
    wavehourse_code: "* Ghi chú: Cột chữ màu đỏ, đơn vị phải nhập nội dung."
  }, {
    wavehourse_code: "Mã kho hàng",
    code: "Mã hàng",
    name: "Tên hàng ",
    group_name: "Tên nhóm hàng",
    group_code: "Mã nhóm hàng",
    unit_name: "Tên đơn vị tính",
    unit_code: "Mã đơn vị tính",
    number_per_invoice: "Số lượng",
    price: "Đơn giá bán",
    vat: "% VAT",
    discount: "% Chiết khấu",
    L: "Trạng thái",
    error_message: "Nội dung lỗi"
  }, {
    wavehourse_code: "(1)",
    code: "(2)",
    name: "(3)",
    group_name: "(4)",
    group_code: "(5)",
    unit_name: "(6)",
    unit_code: "(7)",
    number_per_invoice: "(8)",
    price: "(9)",
    vat: "(10)",
    discount: "(11)",
    L: "(12)",
  }
  ];

  headerStudent = [{
    wavehourse_code: "DANH SÁCH DANH MỤC"
  }, {
    wavehourse_code: "* Ghi chú: Cột chữ màu đỏ, đơn vị phải nhập nội dung."
  }, {
    code: "Mã khoản thu",
    name: "Tên khoản thu ",
    form_revenue_name: "Tên hình thức thu",
    form_revenue: "Mã hình thức thu",
    group_name: "Tên nhóm hàng",
    group_code: "Mã nhóm hàng",
    unit_name: "Tên đơn vị tính",
    unit_code: "Mã đơn vị tính",
    price: "Đơn giá bán",
    vat: "% VAT",
    L: "Trạng thái",
    error_message: "Nội dung lỗi"
  }, {
    code: "(1)",
    name: "(2)",
    form_revenue_name: "(3)",
    form_revenue: "(4)",
    group_name: "(5)",
    group_code: "(6)",
    unit_name: "(7)",
    unit_code: "(8)",
    price: "(9)",
    vat: "(10)",
    discount: "(11)",
    L: "(12)",
  }
  ];

  dataFail = new Array();
  stopImport = false;
  isDisable = false;
  visibleGetError = false;
  visibleProgressBar = false;
  thongbao = "";
  countAll: any;
  importSuccess: any;
  timeRemain = "";
  _translate: any;
  code: any;
  name: any;
  isTruongHoc = false;
  extras = [];
  constructor(
    private productsService: ProductsService,
    private modalService: BsModalService,
    private authService: AuthService,
    private cdr: ChangeDetectorRef,
    private route: ActivatedRoute,
    public translate: TranslateService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }

  ngOnInit() {
    this.extras = [{
      id: "1",
      name: this._translate.CONTROLL.BUTTON.extend,
      items: [
        {
          id: "XOA",
          name: this._translate.CONTROLL.BUTTON.delete,
          icon: this._translate.ICONS.delete
        },
        {
          id: "LAY_FILE_MAU",
          name: this._translate.CONTROLL.BUTTON.get_the_sample_file,
          icon: this._translate.ICONS.download
        }, {
          id: "NAP_DU_LIEU",
          name: this._translate.CONTROLL.BUTTON.import_data,
          icon: this._translate.ICONS.import_data
        },
      ]
    }];
    this.currentUser = this.authService.getCurrentUser();
    this.statusFilter = VSelectBoxService._statusFilter(this._translate);
    if (this.currentUser.account) {
      if (this.currentUser.account.account_type == "HS-SV") {
        this.code = this._translate.GRID.REVENUE.revenue_code;
        this.name = this._translate.GRID.REVENUE.revenue_name;
        this.isTruongHoc = true;
      }
      else {
        this.code = this._translate.GRID.INVOICE_DETAILS.product_code;
        this.name = this._translate.GRID.INVOICE_DETAILS.product_name;
        this.isTruongHoc = false;
      }
    }
    this.loadList();
    this.loadWavehourses();
    this.getAllListbyListtypeByCode();

    this.route.data.subscribe((data: any) => {
      data.configs.forEach(config => {
        if (config.name == "integrate_parameter") {
          this.configCRM = JSON.parse(config.value).integrate_crm;
          if (this.configCRM.is_integrate) {
            this.isIntegrateCRM = true;
          } else {
            this.isIntegrateCRM = false;
            this.cdr.detectChanges();
          }
        }

        if (config.name == "maximum_object_import") {
          this.maximum_object_import = config.value;
        }
        if (config.name == sEnum.configSys.decimal_places_json) {
          this.configDecimal = JSON.parse(config.value);

          if (this.configDecimal.don_gia > 0) {
            let format = "0,###.";
            for (let i = 0; i < this.configDecimal.don_gia; i++) {
              format += "#";
            }
            this.formatter.don_gia = format;
          }
          else
            this.formatter.don_gia = "0,###.##";
        }
      });
    });
    if (this.currentUser.account.account_type == "HS-SV")
      this.dataFail = this.dataFail.concat(this.headerStudent);
    else
      this.dataFail = this.dataFail.concat(this.header);
  }
  loadList() {
    this.dataSource.store = this.productsService.getAll();
  }

  loadWavehourses() {
    this.productsService.loadAllWavehourses().subscribe((response) => {
      this.wavehourses = response.data['wavehourses'];
    })
  }
  itemExtrasClick(e) {
    if (e.itemData.id == 'XOA') {
      this.delete(null);
    } else if (e.itemData.id == 'LAY_FILE_MAU') {
      this.getFileTemp();
    } else if (e.itemData.id == 'NAP_DU_LIEU') {
      this.napDuLieuTuBangKe();
    }
  }
  add() {
    const product = new Product;
    const initialState = {
      product,
      totalCount: this.dataGrid.instance.totalCount(),
      groupProducts: this.groupProducts,
      revenues: this.revenues,
      unitCalculations: this.unitCalculations,
      vats: this.vats,
      groupProductListTypeId: this.groupProductListTypeId,
      revenuListTypeId: this.revenuListTypeId,
      unitCalculationListTypeId: this.unitCalculationListTypeId,
      vatListTypeId: this.vatListTypeId,
      wavehourses: this.wavehourses,
      configDecimal: this.configDecimal,
    };
    this.bsModalRef = this.modalService.show(AddComponent, {
      class: 'modal-lg full-modal', initialState, backdrop: 'static',
      keyboard: false
    });
    this.modalService.onHide.subscribe(() => {
      if (this.bsModalRef.content.successSaveItem) {
        //this.dataGrid.instance.refresh();
        this.dataSource = {};
        this.loadList();
      }
    });
  }
  integrateCRM() {
    Library.showloading();
    this.productsService.integrateCRM().subscribe((res) => {
      if (res.status == "success") {
        Library.notify(this._translate.NOTIFY.syn_data_success, 'success');
        this.dataSource = {};
        this.loadList();
      } else {
        Library.notify(this._translate.NOTIFY.an_error_occurred, 'error');
      }
      Library.hideloading();
    });
  }

  edit(data) {
    let id = data ? data.id : (this.selectedRows[0].length == 0 ? "" : this.selectedRows[0]);
    if (!id) {
      Library.notify(this._translate.NOTIFY.select_object, 'error');
      return;
    } else {
      Library.showloading();
      this.productsService.getProduct(id).subscribe(response => {
        let product = response.data.product;
        const initialState = {
          product,
          groupProducts: this.groupProducts,
          revenues: this.revenues,
          unitCalculations: this.unitCalculations,
          vats: this.vats,
          groupProductListTypeId: this.groupProductListTypeId,
          revenuListTypeId: this.revenuListTypeId,
          unitCalculationListTypeId: this.unitCalculationListTypeId,
          vatListTypeId: this.vatListTypeId,
          wavehourses: this.wavehourses,
          configDecimal: this.configDecimal,
        };
        this.bsModalRef = this.modalService.show(AddComponent, {
          class: 'modal-lg full-modal', initialState, backdrop: 'static',
          keyboard: false
        });
        this.modalService.onHide.subscribe(() => {
          if (this.bsModalRef.content.successSaveItem) {
            //this.dataGrid.instance.refresh();
            this.dataSource = {};
            this.loadList();
          }
        });
        Library.hideloading();
      });

    }
  }

  delete(data) {
    let id = data ? data.id : (this.selectedRows.length == 0 ? "" : this.selectedRows.join(','));
    if (!id) {
      Library.notify(this._translate.NOTIFY.select_object, 'error');
      return;
    } else {
      var result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
      if (result) {
        result.then((dialogResult) => {
          if (dialogResult) {
            this.productsService.delete(id).subscribe((response: any) => {
              if (response.status === "success") {
                Library.notify(this._translate.NOTIFY.delete_success, 'success');
                this.dataSource = {};
                this.loadList();
              } else {
                Library.notify(this._translate.NOTIFY.an_error_occurred, 'error');
              }
            });
          }
        });
      }
    }
  }

  setStatus(data) {
    if (data.status == "HOAT_DONG") {
      return this._translate.GRID.GENERAL.active;
    } else {
      data.status = "KHONG_HOAT_DONG";
      return this._translate.GRID.GENERAL.inactive;
    }
  }

  getAllListbyListtypeByCode() {
    this.productsService.getAllListbyListtypeByCode('DM_NHOM_HANG_HOA,DM_DON_VI_TINH,DM_TY_LE_VAT,DM_HINH_THUC_THU').subscribe((response) => {
      this.groupProducts = response.data['DM_NHOM_HANG_HOA'];
      this.revenues = response.data['DM_HINH_THUC_THU'];
      this.groupProductListTypeId = response.data['ids']['DM_NHOM_HANG_HOA'];
      this.revenuListTypeId = response.data['ids']['DM_HINH_THUC_THU'];
      this.unitCalculations = response.data['DM_DON_VI_TINH'];
      this.unitCalculationListTypeId = response.data['ids']['DM_DON_VI_TINH'];
      this.vats = response.data['DM_TY_LE_VAT'];
      this.vatListTypeId = response.data['ids']['DM_TY_LE_VAT'];
      if (this.currentUser.account.account_type != "HS-SV")
        this.groupProducts = this.groupProducts.filter(p => p.code != "HOCTHEM" && p.code != "HOCPHI" && p.code != "CACKHOANKHAC");
    })
  }

  getFileTemp() {
    this.showPopupGetFile = true;
  }

  fileCoDuLieu() {
    Library.showloading();
    this.productsService.getFileTempData().subscribe((res: any) => {
      if (res.status == 'success') {
        let file = res.data.product;
        Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
      } else {
        Library.notify(res.message, 'error');
      }
      this.showPopupGetFile = false;
      Library.hideloading();
    });
  }

  fileTrang() {
    Library.showloading();
    this.productsService.getFileTemp().subscribe((res: any) => {
      if (res.status == 'success') {
        let file = res.data.product;
        Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
      } else {
        Library.notify(res.message, 'error');
      }
      this.showPopupGetFile = false;
      Library.hideloading();
    });
  }
  thayTheToanBo() {
    this.isDisable = true;
    this.import(this.dataExcelFile, 'THAY_THE');
    this.dataExcelFile = null;
  }

  themTiep() {
    this.isDisable = true;
    this.import(this.dataExcelFile, 'THEM_TIEP');
    this.dataExcelFile = null;
  }

  // huy() {
  //   this.showPopup = false;
  //   this.dataExcelFile = null;
  // }

  napDuLieuTuBangKe() {
    setTimeout(() => {
      document.getElementById("file-bang-ke").click();
    }, 50);
  }

  onFileChanged(evt) {
    Library.showloading();
    const target: DataTransfer = <DataTransfer>(evt.target);
    console.log(target)

    this.fileName = target.files[0].name;
    this.fileSize = target.files[0].size / 1024;
    if (target.files.length !== 1) {
      Library.notify(this._translate.NOTIFY.not_select_multi, 'error');
      throw new Error(this._translate.NOTIFY.not_select_multi);
    };
    const reader: FileReader = new FileReader();

    reader.onload = (e: any) => {
      /* read workbook */
      const bstr: string = e.target.result;
      const wb: XLSX.WorkBook = XLSX.read(bstr, { type: 'binary' });

      /* grab first sheet */
      const wsname: string = wb.SheetNames[0];
      const ws: XLSX.WorkSheet = wb.Sheets[wsname];

      /* save data */
      let rangeRow = 5;
      if (this.currentUser.account.account_type == "HS-SV")
        rangeRow = 4;
      let data = <any>(XLSX.utils.sheet_to_json(ws, { header: "A", range: rangeRow }));
      let fileBangKe: any = document.getElementById("file-bang-ke");
      fileBangKe.value = "";
      //data = data.filter(p=>p.B || p.C);
      this.dataExcelFile = data;
      this.countAll = data.length;
      this.showPopup = true;
      Library.hideloading();
    };
    reader.readAsBinaryString(target.files[0]);
  }

  import(dataExcelFile, type) {
    this.visibleProgressBar = true;
    let data = [];
    if (dataExcelFile) {
      let _index = 0;
      dataExcelFile.forEach(element => {
        let isValid = true;
        if (this.currentUser.account.account_type == "HS-SV") {
          if (!element.A || !element.B || !element.C || !element.D) {
            let product = new Object;
            product["code"] = element.A ? element.A : "";
            product["name"] = element.B ? element.B : "";
            product["form_revenue_name"] = element.C ? element.C : "";
            product["form_revenue"] = element.D ? element.D : "";
            product["group_name"] = element.E ? element.E : "";
            product["group_code"] = element.F ? element.F : "";
            product["unit_name"] = element.G ? element.G : "";
            product["unit_code"] = element.H ? element.H : "";
            product["price"] = element.I ? element.I : "";
            product["vat"] = element.J ? element.J : "";
            product["note"] = element.K ? element.K : "";
            product["error_message"] = "Lỗi thiếu tên hàng";
            if (element.B || element.C) {
              ++_index;
              this.dataFail.push(product);
            }
          } else {
            ++_index;
            let product = new Product();
            product.code = element.A ? element.A : "";
            product.name = element.B ? element.B : "";
            product.form_revenue = element.D ? element.D : "";
            product.group_code = element.F ? element.F : "";
            product.unit_code = element.H ? element.H : "";
            product.price = element.I ? element.I.replace(new RegExp(",", 'g'), "") : "";
            this.vats.forEach(vat => {
              if (element.J && vat.name == element.J) {
                product.vat = vat.code;
              }
            });
            product.note = element.K ? element.K : "";
            product.order = 1;
            product["status"] = "HOAT_DONG"
            if (product.code != "" && !this.productCodePattern.test(product.code))
              isValid = false;
            if (!isValid) {
              product["error_message"] = this._translate.VALIDATION.required_productCode;
              this.dataFail.push(product);
            }
            else
              data.push(product);
          }
        }
        else {
          if (!element.C) {
            let product = new Object;
            product["wavehourse_code"] = element.A ? element.A : "";
            product["code"] = element.B ? element.B : "";
            product["name"] = element.C ? element.C : "";
            product["group_name"] = element.D ? element.D : "";
            product["group_code"] = element.E ? element.E : "";
            product["unit_name"] = element.F ? element.F : "";
            product["unit_code"] = element.G ? element.G : "";
            product["number_per_invoice"] = element.H ? element.H : "";
            product["price"] = element.I ? element.I : "";
            product["vat"] = element.J ? element.J : "";
            product["discount"] = element.K ? element.K : "";
            product["error_message"] = "Lỗi thiếu tên hàng";
            if (element.B) {
              ++_index;
              this.dataFail.push(product);
            }
          } else {
            ++_index;
            let product = new Product();
            product.code = element.B ? element.B : "";
            product.name = element.C ? element.C : "";
            product.group_code = element.E ? element.E : "";
            product.unit_code = element.G ? element.G : "";
            product.number_per_invoice = element.H ? element.H : "";
            product.price = element.I ? element.I.replace(new RegExp(",", 'g'), "") : "";
            this.vats.forEach(vat => {
              if (element.J && vat.name == element.J) {
                product.vat = vat.code;
              }
            });
            product.discount = element.K ? element.K : "";
            product.note = element.N ? element.N : "";
            product.order = 1;
            product["status"] = "HOAT_DONG";
            if (product.code != "" && !this.productCodePattern.test(product.code))
              isValid = false;
            if (!isValid) {
              product["error_message"] = this._translate.VALIDATION.required_productCode;
              this.dataFail.push(product);
            }
            else
              data.push(product);
          }
        }
      });
      this.countAll = _index;
    }
    let _lst = data.filter(p => p.name);
    if (_lst.length > 0) {
      if (type == "THAY_THE") {
        this.productsService.clearData().subscribe((res: any) => {
          if (res.status == "success") {
            this.saveImport(_lst, 1);
          } else {
            Library.notify(this._translate.NOTIFY.an_error_occurred, "error")
          }
        })
      } else if (type == "THEM_TIEP") {
        this.saveImport(_lst, 1);
      }
    } else {
      Library.notify(this._translate.NOTIFY.file_empty, "error")
    }
  }

  saveImport = (data, page) => {
    if (this.stopImport == true) {
      return;
    }

    let numberPages = Math.floor(data.length / this.maximum_object_import) + 1;
    let percentComplete = 100 / numberPages;
    let timePerRow = 7;
    let timeTotal = (numberPages) * timePerRow;
    let pageData = {
      "products": this.paginate(data, this.maximum_object_import, page)
    };

    this.productsService.importData(pageData).subscribe((res: any) => {
      timeTotal = timeTotal - (page * timePerRow)
      this.timeRemain = `, ${this._translate.NOTIFY.time_remaining}: ${moment.utc(timeTotal * 1000).format('HH:mm:ss')}.`

      if (res.status == "success") {
        this.valueProgress += percentComplete;
        if (page < numberPages) {
          this.saveImport(data, page + 1)
        }

        if (res.data.product_errors.length != 0) {
          res.data.product_errors.forEach(error => {
            delete error.id;
            delete error.account_id;
            delete error.group_code;
            delete error.student_reduce_rate_code;
            delete error.order;
            delete error.status;
            if (error.object_code == "CA_NHAN") {
              error.object_code = "X";
            } else {
              error.object_code = "";
            }
          });
          let tr = this.dataFail.length;
          this.dataFail = this.dataFail.concat(res.data.product_errors);
        }
        let _soBanGhiLoi = (res.data.product_errors ? res.data.product_errors.length : 0);
        this.importSuccess = this.countAll - this.dataFail.length + 4;
        let banghiloi = this.countAll - this.importSuccess + _soBanGhiLoi;
        //  this.importSuccess = this.countAll - _soBanGhiLoi;
        if (page == numberPages) {
          this.valueProgress = 100;
          this.productsService.confirmClearData().subscribe((res: any) => {
            if (res.status == "success") {
              this.thongbao = `${this._translate.NOTIFY.import} ${this.importSuccess}/${this.countAll} bản ghi (Có ${banghiloi} bản ghi lỗi.)`;
              this.timeRemain = "";
              if (this.importSuccess < this.countAll) {
                this.visibleGetError = true;
                // this.layBanGhiLoi(res.data.product_errors);
              }
              else {
                this.showPopup = false;
                Library.notify(this._translate.NOTIFY.import_success, 'success');
              }
            }
          })
        }

      }
    });
  }
  format = (value) => {
    let percent = Math.round(value * 100);
    return `${this._translate.NOTIFY.finish}: ` + percent + '%' + this.timeRemain;
  }

  paginate(array, page_size, page_number) {
    --page_number; // because pages logically start with 1, but technically with 0
    return array.slice(page_number * page_size, (page_number + 1) * page_size);
  }

  onImportShowing(e) {
    this.stopImport = false;
  }
  onImportHiding(e) {
    let r = true;
    if (this.valueProgress < 100) {
      r = confirm(this._translate.CONFIRM.import_data);
    }
    if (r) {
      this.stopImport = true;
      this.dataSource = {};
      this.loadList();
      this.valueProgress = 0;
      this.isDisable = false;
      this.thongbao = "";
      this.dataFail = new Array();
      if (this.currentUser.account.account_type == "HS-SV")
        this.dataFail = this.dataFail.concat(this.headerStudent);
      else
        this.dataFail = this.dataFail.concat(this.header);
    } else {
      e.cancel = true;
    }
  }

  layBanGhiLoi(dataFail) {
    if (this.valueProgress < 100) {
      Library.notify(this._translate.NOTIFY.import_error, "error");
    } else {
      const ws: XLSX.WorkSheet = XLSX.utils.json_to_sheet(this.dataFail);
      const wb: XLSX.WorkBook = XLSX.utils.book_new();
      var wscols = [
        { wpx: 130 }, // "pixels"
        { wpx: 130 },
        { wpx: 200 },
        { wpx: 130 },
        { wpx: 130 },
        { wpx: 80 },
        { wpx: 80 },
        { wpx: 80 },
        { wpx: 80 },
        { wpx: 80 },
        { wpx: 80 },
        { wpx: 100 },
        { wpx: 400 }
      ];
      var wsrows = [
        { hidden: true }, // hide row
      ];
      ws['!cols'] = wscols;
      ws['!rows'] = wsrows;
      XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');

      XLSX.writeFile(wb, 'danh_sach_loi.xlsx');
    }
  }
}
