import { Component, OnInit, AfterViewInit, ViewChild } from '@angular/core';
import { Library, AuthService } from '@core/index';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { List, ListsService } from '@modules/lists/shared';
import { BsModalService } from 'ngx-bootstrap/modal';
import { DxFormComponent } from 'devextreme-angular';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-add-revenue',
  templateUrl: './add-revenue.component.html',
  styleUrls: ['./add-revenue.component.scss']
})
export class AddRevenueComponent implements OnInit, AfterViewInit {
  @ViewChild(DxFormComponent) form: DxFormComponent;
  listTypeId: string;
  revenue: any;
  onSave: any;
  _translate: any;
  txt: any = {};
  selectedItemBefore: any[] = [];
  selectedItemAfter: any[] = [];
  allowDeletingBefore: boolean = false;
  deleteTypeBefore: string = "toggle";
  allowDeletingAfter: boolean = false;
  deleteTypeAfter: string = "toggle";
  isHinhThucThu = false;
  monthBefore: any[] = [
    {
      name: "Tháng 1",
      code: "01/T"
    },
    {
      name: "Tháng 2",
      code: "02/T"
    },
    {
      name: "Tháng 3",
      code: "03/T"
    },
    {
      name: "Tháng 4",
      code: "04/T"
    },
    {
      name: "Tháng 5",
      code: "05/T"
    },
    {
      name: "Tháng 6",
      code: "06/T"
    },
    {
      name: "Tháng 7",
      code: "07/T"
    },
    {
      name: "Tháng 8",
      code: "08/T"
    },
    {
      name: "Tháng 9",
      code: "09/T"
    },
    {
      name: "Tháng 10",
      code: "10/T"
    },
    {
      name: "Tháng 11",
      code: "11/T"
    },
    {
      name: "Tháng 12",
      code: "12/T"
    }
  ];
  monthAfter: any[] = [
    {
      name: "Tháng 1",
      code: "01/S"
    },
    {
      name: "Tháng 2",
      code: "02/S"
    },
    {
      name: "Tháng 3",
      code: "03/S"
    },
    {
      name: "Tháng 4",
      code: "04/S"
    },
    {
      name: "Tháng 5",
      code: "05/S"
    },
    {
      name: "Tháng 6",
      code: "06/S"
    },
    {
      name: "Tháng 7",
      code: "07/S"
    },
    {
      name: "Tháng 8",
      code: "08/S"
    },
    {
      name: "Tháng 9",
      code: "09/S"
    },
    {
      name: "Tháng 10",
      code: "10/S"
    },
    {
      name: "Tháng 11",
      code: "11/S"
    },
    {
      name: "Tháng 12",
      code: "12/S"
    }
  ];
  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    private listsService: ListsService,
    private bsModalService: BsModalService,
    public translate: TranslateService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }
  ngOnInit() {
    this.txt = {
      active: this._translate.GRID.GENERAL.active,
    }
  }

  focusField = () => {
    this.bsModalService.onShown.subscribe(() => {
      this.form.instance.getEditor("code").focus();
    });
  }

  ngAfterViewInit() {
    this.focusField();
  }

  onSubmit = function (e) {
    e.preventDefault();
    if (this.revenue.status) {
      this.revenue.status = "HOAT_DONG";
    } else {
      this.revenue.status = "KHONG_HOAT_DONG";
    }
    this.revenue.listtype_id = this.listTypeId;
    const itemBefor = this.selectedItemBefore.length === 0 ? '' : this.selectedItemBefore.map((obj: any) => obj.code).join(';');
    const itemAfter = this.selectedItemAfter.length === 0 ? '' : this.selectedItemAfter.map((obj: any) => obj.code).join(';');
    if (itemBefor != '') {
      this.revenue.note = itemBefor;
      if (itemAfter != '')
        this.revenue.note += ";" + itemAfter;
    }
    else {
      if (itemAfter != '')
        this.revenue.note = itemAfter;
    }
    let data = { list: this.revenue };
    this.listsService.add(data).subscribe((response: any) => {
      if (response.status == "success") {
        Library.notify(this._translate.NOTIFY.add_success, "success");
        this.onSave(this.revenue);
        this.bsModalRef.hide();
      } else {
        Library.notify(response.message, "error");
      }
    }, err => { }, () => {
    });
  }
}
